.song-container {
  padding: 1rem;
  display: flex;
  height: 100%;
  align-items: center;
  h2 {
    font-size: 3rem;
    display: inline;
  }
  h3 {
    font-size: 2rem;
  }
  .subtext {
    font-size: 1.5rem;
    font-family: "Zen Kurenaido", sans-serif;
    direction: rtl;
  }
  .icons {
    color: #686a6b;
    padding: 0.1rem;
    margin: 0;
    font-size: 2.75rem;
  }
}

.year {
  font-weight: normal;
  font-size: 2.3rem;
  display: inline;
  //padding: 0 1.7vmin;
  color: #7e7f85;
  transition: .25s ease;
}

.year-active {
  color: #1c1c1c !important;
}
