.show-item {
  background-color: #e8e8e8;
  margin: 0.6rem;
  justify-content: space-between;
  border-radius: 5px;
  font-size: 1.5rem;
  border-left: 6px solid #ccc!important;

  .align-right {
    display: inline;
    float:right;
    margin-left: auto;
    margin-right: 0;
  }
  .color-btn {
    background-color: #a30cc5;
    border-radius: 0 5px 5px 0;
    font-weight: bolder;
  }
  .nav-link {
    color: #fff;
  }
}

.carousel-show {
  width: 100%;
  justify-content: center;
}
.carousel-inner{
  border-radius: 5px;
}
.width-700 {
  max-width: 700px;
}
