.album-art-small {
  width: 90%;
  max-width: 300px;
  border-radius: 3px;
  -webkit-box-shadow: 5px 5px 15px 9px rgba(0,0,0,0.25);
  box-shadow: 5px 5px 15px 9px rgba(0,0,0,0.25);
}
.crop-square {
  object-fit: cover;
  width:300px;
  height:300px;
  object-position: bottom;
}

.dark-bg {
  background-color: #e7e7e7;
  border-radius: 5px;
}
.nav-link {
  padding: 0;
}
.video-container {
  position: relative;
  padding-bottom: 56.25%; /* proportion value to aspect ratio 16:9 (9 / 16 = 0.5625 or 56.25%) */
  height: 0;
  overflow: hidden;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.header-offset {
  margin-top: $nav-height;
}

.page-title {
  font-style: normal;
  font-weight: normal;
  font-size: 2.5rem;
}

.create-icon {
  font-size: 1.8rem;
}

.users {
  margin: 0.8rem;
  padding: 0.6rem;
  font-size: 1.5rem;
  background-color: #e8e8e8;
  border-radius: 3px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .btn {
    font-weight: bolder;
  }
}

.main-content {
  transition: .4s ease;
}

.ease-transition {
  transition: .25s ease;
}

.onclick{
  cursor: pointer;
  transition: .2s ease;
}
.onclick:hover {
  filter: brightness(20%);
}
