.under-banner {
  box-shadow: 0 0 36px 13px rgba(0,0,0,0.56);
  width: 100%;
  display: block;
  z-index: 10;
  position: relative;
  top: -2rem;
  margin-bottom: -2rem;
  background-color: #f6f6f6;
  min-height: 100%;
  padding: 1rem;
  justify-content: center;
}
.home-img {
  object-fit: cover;
  width: 70%;
  height: 85%;
  max-width: 50vw;
  border-radius: 5px;
}
.color-strip {
  background-color: #e3a61a;
  height: 100%;
  padding: 0.8rem;
  text-align: center;
  display:table-cell
}

.dark-strip {
  background-color: #2e2f31;
  height: 100%;
  width: 100%;
  padding: 1rem;
}

.home-icons {
  font-size: 2.3rem;
  color: #ffffff;
  vertical-align: center;
  padding-bottom: 0.6rem;
}

.home-layout {
  padding: 1rem;
  margin: 1rem;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  h2 {
    font-size: 3rem;
  }
}
