.banner-video {
  width: 100%;
  filter: brightness(0.7) saturate(0.7) contrast(0.8);
  background-color: #5d5d5d;
}
.banner-container {
  overflow:hidden;
  background-size : contain;
  max-height: 85vh;
  width: 100%;
  z-index: 0;
}

.grain-overlay {
  z-index: 1;
  position: absolute;
  filter: opacity(20%);
  width: 100%;
  height: 100%;
  background-repeat: repeat;
  top:-1vh;
  background-image: url(data:image/png;base64,);
}
