nav {
  padding: 0.25em;
  height: $nav-height;
  //border-bottom: 2px solid #efefef;
  display: flex;
  justify-content: center;
  align-items: center;
  //backdrop-filter: blur(2px);
  position: fixed;
  width: 100%;
  transition: .25s ease;
  .nav-logo {
    max-height: $nav-height - 0.5;
    min-height: 75%;
  }
  .header-link{
    font-size: calc($nav-height / 2);
    text-decoration: none;
    color: #dedede;
    font-style: italic;
    font-weight: lighter;
  }
  .link-active {
    filter: lighten(#fff, 100%) !important;
  }
  .nav-dark {
    filter: invert(1);
  }
}
.white-bg {
  background-color: #f1f1f1;
  -webkit-box-shadow: 0px 0px 24px 5px rgba(0,0,0,0.33);
  box-shadow: 0px 0px 24px 5px rgba(0,0,0,0.33);
}

.width-40 {
  width: 40%;
}

@media (max-width: 1200px) {
  .width-40 {
    width: 80%;
  }
}