html, body {
  margin: 0;
  background-color: #f6f6f6;
  min-width: 700px;
}

.fill-height {
  height: 100% !important;
}

.main {
  padding: 1em 0;
}

.margin-left-sm {
  margin-left: 0.5em;
}
.margin-left-md {
  margin-left: 1.5em;
}
.margin-top-sm {
  margin-top: 0.5em ;
}
.margin-top-md {
  margin-top: 10em !important;
}
.margin-sm {
  margin: 0.5rem;
}

.margin-md {
  margin: 1em;
}

.padding-md {
  padding: 1em;
}
.padding-sm {
  padding: 0.6em;
}
.fill-middle {
  margin-top: 15vh;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  vertical-align: middle;
  justify-content: center;
  justify-items: center;
  text-align: center;
}

.align-text-center {
  text-align: center !important;
}
.align-text-right {
  text-align: right !important;
}

.align-img-center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  vertical-align: middle;
}
.body-container {
  max-width: 1000px;
  padding: 1em;
  margin-right: auto;
  margin-left: auto;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.align-center-end {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.fill-height {
  height: 100%;
}
